import React, { useEffect, useRef, useState } from 'react';
import HistoryStyles from './History.style';

const History = (props) => {
    const {
        theme
    } = props;

    const MainDom = useRef(null);

    const [listContent, setListContent] = useState([
        {
            id: 1,
            year: '2006',
            content: 'Trường Đại học Công nghệ Thông tin, khoa Hệ Thống Thông tin được thành lập.',
            posTop: null,
        },
        {
            id: 2,
            year: '2008-2009',
            content: 'Xây dựng đề án trang bị phòng thí nghiệm khoa Hệ thống Thông tin',
            posTop: null,
        },
        {
            id: 3,
            year: '2011-2012',
            content: 'Phòng thí nghiệm Hệ thống Thông tin đi vào hoạt động.',
            posTop: null,
        },
        {
            id: 4,
            year: '2013-2017',
            content: 'TS. Ngô Thanh Hùng Phụ trách, 2 nghiên cứu viên và sinh viên tham gia nghiên cứu tại phòng thí nghiệm.',
            posTop: null,
        },
        {
            id: 5,
            year: '2017-2020',
            content: 'Phó GS Nguyễn Đình Thuân phụ trách.',
            posTop: null,
        },
        {
            id: 6,
            year: '2021',
            content: 'TS. Nguyễn Thanh Bình phụ trách.',
            posTop: null,
        },
    ]);

    useEffect(() => {
        calcPos();
        window.addEventListener('resize', calcPos);

        return () => {
            window.removeEventListener('resize', calcPos);
        }
    }, [])

    const calcPos = () => {
        let elements = MainDom.current.querySelectorAll('.right-content ul li');
        let arrayPos = [];
        let listClone = JSON.stringify(listContent);
        listClone = JSON.parse(listClone);

        Array.from(elements).map(e => {
            arrayPos.push(e.offsetTop);
        })

        listClone.map((e, i) => {
            e.posTop = arrayPos[i];
        })

        setListContent(listClone);
    }

    const renderRightContent = () => {
        return listContent.map((e, i) => {
            return (
                <li key={i}><p>{e.content}</p></li>
            )
        })
    }

    const renderLeftContent = () => {
        return listContent.map((e, i) => {
            return (
                <li key={i} style={{top: e.posTop + 'px'}}><p>{e.year}</p></li>
            )
        })
    }

    return (
        <HistoryStyles ref={MainDom} className='d-flex align-items-center'>
            <div className="container">
                <div className="grid-container">
                    <div className="left-timeline">
                        <ul>
                            {renderLeftContent()}
                        </ul>
                    </div>
                    <div className="col"></div>
                    <div className="right-content">
                        <ul>
                            {renderRightContent()}
                        </ul>
                    </div>
                </div>
            </div>
        </HistoryStyles>
    )
}

export default History;
