import styled from 'styled-components';

export default styled.div`
height: 100vh;
overflow-y: auto;
border-left: 1px solid ${props => props.theme.border};
background-color: ${props => props.theme.bgColor_2};
@media (max-width: 993px) {
    border-left: none;
}
.grid-container{
    width: 500px;
    margin: auto;
    grid-template-columns: 100px 4px auto;
    gap: 20px;
    padding: 40px 10px;
    position: relative;
    .col{
        width: 100%;
        height: 100%;
        background-color: ${props => props.theme.primary};
    }
    .left-timeline{
        ul {
            height: 100%;
            li{
                position: absolute;
                width: 100px;
                p{
                    text-align: right;
                }
            }
        }
    }
    .right-content{
        ul {
            li{
                margin: 20px 0;
                p{
                    font-size: 14px;
                }
            }
        }
    }
    @media(max-width: 993px) {
        padding-top: 114px;
    }
    @media (max-width: 510px) {
        grid-template-columns: 50px 4px auto;
        gap: 10px;
        width: 300px;
        .left-timeline{
            ul {
                li{
                    width: 50px;
                    p{
                        font-size: 10px;
                    }
                }
            }
        }
        .right-content{
            ul {
                li{
                    p{
                        font-size: 12px;
                    }
                }
            }
        }
    }
}
`;